import styled from 'styled-components';
import { Container } from '../container';
import { colors, media, font } from '../../constants/theme';

export const StyledHeader = styled.div`
  @media ${media.notMobile} {
    background-color: ${({ darkTheme }) => (darkTheme ? colors.brand : '#fff')};
  }
  @media ${media.mobile} {
    background-color: ${({ darkTheme }) =>
      darkTheme ? colors.darkBg : 'transparent'};
  } ;
`;

export const StyledContainer = styled(Container)`
  display: flex;
  align-content: center;
  align-items: center;

  @media ${media.condensedNav} {
    height: 80px;
  }
  @media ${media.fullNav} {
    height: 120px;
  }
`;

export const StyledLogo = styled.div`
  @media ${media.condensedNav} {
    height: 45px;
  }
  @media ${media.fullNav} {
    height: 60px;
  }

  svg {
    height: 100%;
  }
`;

export const StyledNav = styled.nav`
  margin-left: auto;

  ul {
    display: flex;
  }

  li {
    padding: 0 1.2rem;
  }

  a {
    display: block;
    font-weight: ${font.weight.navLinks};
    color: ${({ darkTheme }) => (darkTheme ? '#fff' : colors.brand)};
    padding: 0.8rem 0;
    position: relative;

    &:after {
      content: '';
      display: block;
      position: absolute;
      left: 50%;
      bottom: 0;
      height: 3px;
      width: 0;
      background: ${({ darkTheme }) => (darkTheme ? '#fff' : colors.brand)};
      transition: width 200ms ease-in-out 0s, left 200ms ease-in-out 0s;
    }

    &:hover:after {
      width: 100%;
      left: 0;
    }
  }
`;

export const StyledMobileToggle = styled.button`
  /* Resets */
  border: none;
  margin: 0;
  padding: 0;
  width: auto;
  overflow: visible;
  background: transparent;
  cursor: pointer;
  font: inherit;
  line-height: normal;
  user-select: none;
  -webkit-font-smoothing: inherit;
  -moz-osx-font-smoothing: inherit;
  -webkit-appearance: none;

  /* Styles */
  margin-left: auto;
  z-index: 60;
  width: 2rem;
  height: 2rem;
  ${({ menuOpen }) =>
    menuOpen
      ? `
      position: fixed;
      right : 24px;
      `
      : `
      position: relative;
  `};

  &:focus {
    outline: none;
  }

  span {
    position: absolute;
    width: 2rem;
    height: 2px;
    border-radius: 2px;
    background-color: ${({ darkTheme }) => (darkTheme ? '#fff' : colors.brand)};
    transition: 250ms ease-in-out;
    opacity: 1;
    left: 0;

    &:nth-child(1) {
      top: calc(1rem - 0.3rem);
    }
    &:nth-child(2) {
      top: calc(1rem + 0.3rem);
    }

    ${({ menuOpen }) =>
      menuOpen &&
      `
      background-color: #fff;

			&:nth-child(1) {
        transform: rotate(-45deg);
        top: 1rem;
			}
			&:nth-child(2) {
        transform: rotate(45deg);
        top: 1rem;
			}
    `};
  }
`;

export const StyledMobileNav = styled.nav`
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  background-color: ${colors.darkBg};
  color: #fff;
  position: fixed;
  transition: top 250ms ease-in-out;
  top: ${({ menuOpen }) => (menuOpen ? '0' : '-100%')};
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 50;

  svg {
    height: 8rem;
    padding: 0 2.3rem;
    margin-bottom: 1.5rem;
  }

  ul {
    width: 100%;
  }

  a {
    font-size: 20px;
    display: block;
    color: #fff;
    padding: 1rem 2.5rem;
    top: ${({ menuOpen }) => (menuOpen ? '0' : '-100%')};

    /* a {
			transform: translateY(0);
		}
		&:nth-child(1) a {
			transition-delay: 300ms;
			@include screen-md-up {
				transition-delay: 1050ms;
			}
		} */
  }
`;
