import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { createGlobalStyle } from 'styled-components';

// Const & Utils
import { useWindowSize } from '../../utils/useWindowSize';
import { mediaSize } from '../../constants/theme';

// Components
import { LogoLight, LogoDark, LogoStackedLight } from '../logos';
import {
  StyledHeader,
  StyledContainer,
  StyledLogo,
  StyledNav,
  StyledMobileToggle,
  StyledMobileNav,
} from './styles';
import { Link } from '../../components';

// Data
import data from '../../data/header.json';
// import services from '../../data/services.json';

const createNavSection = (data, isMobile) => {
  return (
    <ul>
      {data.map((link, index) => (
        <li key={index}>
          <Link to={link.src}>{isMobile ? link.labelMobile : link.label}</Link>
          {/* {link.children &&
          link.children.length > 0 &&
          createNavSection(link.children)} */}
        </li>
      ))}
    </ul>
  );
};

const PreventBodyOverflowStyles = createGlobalStyle`
body {
  overflow: hidden;
}
`;

export const Header = ({ darkTheme }) => {
  const [menuOpen, setMenuOpen] = useState(false);
  const { nav } = data;

  const handleMenuOpen = () => setMenuOpen(!menuOpen);

  // Add services sub links
  // nav.services.children = Object.values(services.data).map((page) => {
  //   return {
  //     label: page.title,
  //     src: page.path,
  //   };
  // });

  const condensedNav = useWindowSize().width <= mediaSize.condensedNav;

  return (
    <StyledHeader darkTheme={darkTheme}>
      <StyledContainer>
        {condensedNav ? (
          <>
            {menuOpen && <PreventBodyOverflowStyles />}
            <Link to="/">
              <StyledLogo>
                {darkTheme ? <LogoLight /> : <LogoDark />}
              </StyledLogo>
            </Link>
            <StyledMobileToggle
              menuOpen={menuOpen}
              darkTheme={darkTheme}
              onClick={handleMenuOpen}>
              <span></span>
              <span></span>
            </StyledMobileToggle>
            <StyledMobileNav menuOpen={menuOpen}>
              <LogoStackedLight />
              {createNavSection(Object.values(nav), true)}
            </StyledMobileNav>
          </>
        ) : (
          <>
            <Link to="/">
              <StyledLogo>
                {darkTheme ? <LogoLight /> : <LogoDark />}
              </StyledLogo>
            </Link>
            <StyledNav menuOpen={menuOpen} darkTheme={darkTheme}>
              {createNavSection(Object.values(nav))}
            </StyledNav>
          </>
        )}
      </StyledContainer>
    </StyledHeader>
  );
};

Header.propTypes = {
  darkTheme: PropTypes.bool,
};
